.info {
  background: radial-gradient(19.46% 50% at 50% 50%, rgba(227, 232, 236, 0.00) 0%, #D3DAE3 100%);
  padding: 10rem 0 3.75rem;
  .diagram {
    position: relative;
    max-width: 35.625rem;
    margin: 0 auto;
  }
  .diagram__circle {
    animation: spin 5s infinite linear;
  }
  #showrndimages {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 21.875rem;
  }
  .diagram__item {
    position: absolute;
    max-width: 21.25rem;
    display: flex;
    border-bottom: 0.0625rem solid #7A7B7A;
    padding-bottom: 1.5rem;
    &-desc {
      position: relative;
      max-width: 13rem;
      strong {
        font-family: 'Tektur';
        font-size: 2rem;
        line-height: 130%;
        font-weight: 600;
        color: #4E5960;
      }
      p {
        font-size: 1rem;
        line-height: 130%;
        color: #4E5960;
        margin: 0.5rem 0 0 0;
      }
    }
    &-img {
      flex-shrink: 0;
      max-width: 7.8125rem;
    }
  }
  .diagram__item:nth-child(1) {
    top: 0;
    left: -21.25rem;
  }
  .diagram__item:nth-child(2) {
    top: 50%;
    left: -26.25rem;
    transform: translate(0, -50%);
    &:before {
      right: -8.125rem;
      width: 6.25rem;
    }
  }
  .diagram__item:nth-child(3) {
    left: -21.25rem;
    bottom: -1rem;
    &:before {
      right: -8rem;
      width: 6.25rem;
    }
  }
  .diagram__item:nth-child(4) {
    top: 0;
    right: -21.25rem;
  }
  .diagram__item:nth-child(5) {
    right: -24.25rem;
    bottom: 7.8125rem;
    &:before {
      left: -5.6rem;
      width: 3.75rem;
    }
  }
  .left-item {
    &:after {
      content: '';
      position: absolute;
      right: -1rem;
      bottom: -0.4375rem;
      transform: rotate(45deg);
      width: 0.0625rem;
      height: 2.8125rem;
      background: #7A7B7A;
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 1.9rem;
      right: -3.125rem;
      width: 1.25rem;
      height: 0.0625rem;
      background: #7A7B7A;
    }
    .diagram__item-desc {
      text-align: right;
      margin-right: 2rem;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -1.5rem;
        width: 3.5rem;
        height: 0.1875rem;
        background: #7A7B7A;
      }
    }
  }
  .right-item {
    &:after {
      content: '';
      position: absolute;
      left: -1rem;
      bottom: -0.4375rem;
      transform: rotate(-45deg);
      width: 0.0625rem;
      height: 2.8125rem;
      background: #7A7B7A;
    }
    &:before {
      content: '';
      position: absolute;
      bottom: 1.9rem;
      left: -3.125rem;
      width: 1.25rem;
      height: 0.0625rem;
      background: #7A7B7A;
    }
    .diagram__item-desc {
      order: 1;
      text-align: left;
      margin-left: 2rem;
      &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: -1.5rem;
        width: 3.5rem;
        height: 0.1875rem;
        background: #7A7B7A;
      }
    }
  }
}

@media screen and (max-width: 1650px) {
  .info {
    .diagram__item {
      max-width: 16rem;
      &-img {
        max-width: 3.75rem;
      }
    }
    .diagram__item:nth-child(1) {
      left: -16rem;
    }
    .diagram__item:nth-child(2) {
      left: -20.25rem;
    }
    .diagram__item:nth-child(3) {
      left: -16rem;
    }
    .diagram__item:nth-child(4) {
      right: -16rem;
    }
    .diagram__item:nth-child(5) {
      right: -19.25rem;
    }
  }
}

@media screen and (max-width: 1450px) {
  .info {
    .diagram__item {
      max-width: 14rem;
      &-img {
        max-width: 2.5rem;
      }
    }
    .diagram__item:nth-child(1) {
      left: -14rem;
    }
    .diagram__item:nth-child(2) {
      left: -16.25rem;
      &:before {
        right: -6.125rem;
        width: 4.25rem;
      }
    }
    .diagram__item:nth-child(3) {
      left: -11rem;
      &:before {
        right: -6rem;
        width: 4.25rem;
      }
    }
    .diagram__item:nth-child(4) {
      right: -14rem;
    }
    .diagram__item:nth-child(5) {
      right: -15.25rem;
    }
    .left-item {
      .diagram__item-desc {
        margin: 0 1.25rem 0 0;
      }
    }
    .right-item {
      .diagram__item-desc {
        margin: 0 0 0 1.25rem;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .info {
    .diagram {
      max-width: 25rem;
    }
    #showrndimages {
      width: 16rem;
    }
    .diagram__item {
      &-desc {
        strong {
          font-size: 1.5rem;
        }
        p {
          font-size: 0.875rem;
        }
      }
    }
    .diagram__item:nth-child(5) {
      bottom: 5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .info {
    .diagram {
      max-width: 20rem;
    }
    #showrndimages {
      width: 12rem;
    }
    .diagram__item {
      max-width: 13rem;
    }
    .diagram__item:nth-child(1) {
      top: -3.125rem;
      left: -13rem;
    }
    .diagram__item:nth-child(2) {
      left: -14rem;
      &:before {
        right: -3.125rem;
        width: 1.25rem;
      }
    }
    .diagram__item:nth-child(3) {
      left: -13rem;
      &:before {
        right: -3.125rem;
        width: 1.25rem;
      }
    }
    .diagram__item:nth-child(5) {
      right: -14rem;
      bottom: 2.5rem;
      &:before {
        left: -3.125rem;
        width: 1.25rem;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .info {
    .diagram__item {
      padding-bottom: 1rem;
      max-width: 7rem;
      &-img {
        display: none;
      }
      &-desc {
        margin: 0 !important;
        &:after {
          bottom: -1rem !important;
          width: 1.5rem !important;
          height: 0.125rem !important;
        }
        strong {
          font-size: 1.125rem;
        }
        p {
          font-size: 0.6875rem;
        }
      }
    }
    .diagram__item:nth-child(1) {
      left: -7rem;
    }
    .diagram__item:nth-child(2) {
      left: -8rem;
    }
    .diagram__item:nth-child(3) {
      left: -6rem;
    }
    .diagram__item:nth-child(4) {
      right: -8rem;
    }
    .diagram__item:nth-child(5) {
      right: -8rem;
    }
  }
}

@media screen and (max-width: 576px) {
  .info {
    padding-top: 6.25rem;
    .diagram {
      max-width: 14rem;
    }
    #showrndimages {
      width: 9rem;
    }
    .diagram__item {
      max-width: 6rem;
      &-desc {
        strong {
          font-size: 0.875rem;
        }
        p {
          font-size: 0.625rem;
          margin-top: 0.1875rem;
        }
      }
    }
    .diagram__item:nth-child(1) {
      left: -6rem;
    }
    .diagram__item:nth-child(5) {
      right: -7rem;
      bottom: 0;
    }
  }
}

@media screen and (max-width: 499px) {
  .info {
    .diagram {
      max-width: 12rem;
    }
    #showrndimages {
      width: 8rem;
    }
    .diagram__item {
      max-width: 4rem;
      &-desc {
        strong {
          font-size: 0.75rem;
        }
        p {
          font-size: 0.5rem;
        }
      }
    }
    .diagram__item:nth-child(1) {
      left: -4rem;
    }
    .diagram__item:nth-child(2) {
      left: -6rem;
    }
    .diagram__item:nth-child(3) {
      left: -4rem;
    }
    .diagram__item:nth-child(4) {
      right: -6rem;
    }
    .diagram__item:nth-child(5) {
      right: -5rem;
    }
  }
}
