.btn {
  padding: 0.3125rem;
  span {
    min-width: auto;
    padding: 0;
  }
  &:focus,
  &:visited,
  &:active {
    display: flex;
  }
}
.btn-blue {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #1397DE;
  span {
    font-family: 'Tektur';
    line-height: 130%;
    font-weight: 600;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    color: #FFF;
  }
  &:hover {
    background: #87CEF4;
    span {
      color: #4E5960;
    }
  }
  &:active {
    background: #4E5960;
    span {
      color: #ffffff;
    }
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 3rem;
  height: 1.5rem;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    height: 0.1875rem;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
    width: 2.5rem;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
    width: 100%;
  }
  span:nth-child(3) {
    bottom: 0;
    width: 2.5rem;
  }
  &--active {
    span:nth-child(1) {
      top: 0.625rem;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 0.65625rem;
      transform: rotate(45deg);
    }
  }
}
.sidebar {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 8.75rem;
  height: 100vh;
  background: #1397DE;
  padding: 1.875rem 1rem 5rem;
  &__inner {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  .menu2 {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
  }
  .burger {
    span {
      background: #ffffff;
    }
  }
  .menu-text {
    font-family: 'Tektur';
    font-size: 1rem;
    line-height: 105%;
    color: rgba(255, 255, 255, 0.25);
    margin-top: 0.5rem;
  }
  &__text {
    font-size: 4rem;
    line-height: 120%;
    color: rgba(255, 255, 255, 0.25);
    writing-mode: vertical-rl;
    transform: rotate(180deg);
    margin-bottom: 2rem;
  }
  &__menu {
    position: fixed;
    z-index: 5;
    top: 0;
    left: -105%;
    width: 25rem;
    height: 100vh;
    background: #1397DE;
    padding: 6.25rem 3rem 3.125rem;
    transition: all 0.5s;
    &--active {
      left: 0;
    }
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li {
      font-size: 1.5rem;
      line-height: 130%;
      color: #ffffff;
    }
  }
}
.page-top {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 16.875rem 0 21.25rem;
  }
  &__desc {
    position: relative;
    z-index: 1;
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    font-weight: 600;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: #0072AF;
    margin: 0;
    span {
      font-size: 5rem;
      line-height: 130%;
    }
  }
  p {
    max-width: 40.625rem;
    font-size: 1.75rem;
    line-height: 140%;
    color: #4E5960;
    margin: 2.5rem 0 0 0;
    strong {
      font-weight: 700;
    }
  }
  &__info {
    max-width: 40.625rem;
    display: flex;
    align-items: stretch;
    margin-top: 7.1875rem;
  }
  &__btn {
    width: 55%;
    height: 4.8125rem;
    border-radius: 0.625rem;
  }
  &__info-text {
    width: 45%;
    height: 4.8125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #FFF;
    border-radius: 0 0.9375rem 0.9375rem 0;
    padding: 0.3125rem 2.5rem;
    font-size: 1rem;
    line-height: 130%;
    color: #424647;
  }
  &__img {
    position: absolute;
    right: -3.125rem;
    bottom: 0;
    width: 55rem;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
    &:hover {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  &__img2 {
    position: absolute;
    top: 10rem;
    right: -8.125rem;
    width: 19.375rem;
  }
  &__img3 {
    position: absolute;
    right: -7.5rem;
    bottom: 6.5625rem;
  }
  &__bottom {
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    background: rgba(255, 255, 255, 0.70);
    backdrop-filter: blur(0.9375rem);
    padding: 0.9375rem 0;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      max-width: 8.5rem;
      margin-right: 1.875rem;
    }
    &-text {
      max-width: 16.5625rem;
      font-size: 1.25rem;
      line-height: 130%;
      color: #4E5960;
      strong {
        font-weight: 700;
      }
    }
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}


@media screen and (max-width: 1700px) {
  .page-top {
    &__img {
      width: 43.75rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .sidebar {
    &__text {
      font-size: 3rem;
    }
  }
  .page-top {
    &__inner {
      padding: 12.5rem 0 18.75rem;
    }
    &__item {
      &-img {
        max-width: 5rem;
      }
    }
    &__img {
      width: 37.5rem;
    }
    &__img2 {
      right: 0;
      width: 12.5rem;
    }
    &__img3 {
      width: 9.375rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .sidebar {
    width: 5rem;
  }
  .page-top {
    &__inner {
      padding: 11.25rem 0 15.625rem;
    }
    h1 {
      font-size: 2.5rem;
      span {
        font-size: 3.75rem;
      }
    }
    p {
      max-width: 34rem;
      font-size: 1.25rem;
      margin-top: 1.5rem;
    }
    &__info {
      max-width: 34rem;
      margin-top: 3.75rem;
    }
    &__btn {
      width: 50%;
      height: 4.375rem;
      span {
        font-size: 1rem;
      }
    }
    &__info-text {
      height: 4.375rem;
      padding: 0.3125rem 1rem;
    }
    &__img {
      width: 31.25rem;
    }
    &__item {
      &-img {
        margin-right: 1rem;
      }
      &-text {
        font-size: 1.125rem;
      }
    }
  }
}

@media screen and (max-width: 1023px) {
  .sidebar {
    display: none;
  }
  .page-top {
    &__inner {
      padding: 9.375rem 0 12.5rem;
    }
    h1 {
      font-size: 2rem;
      span {
        font-size: 3rem;
      }
    }
    p {
      max-width: 25rem;
      font-size: 1.125rem;
      margin-top: 1.25rem;
    }
    &__info {
      margin-top: 2.5rem;
    }
    &__img {
      width: 30rem;
    }
    &__img2 {
      top: 6rem;
      right: -3.75rem;
    }
    &__item {
      &-img {
        max-width: 3.75rem;
      }
      &-text {
        font-size: 1rem;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .page-top {
    &__inner {
      padding: 8.75rem 0 5rem;
    }
    &__img {
      display: none;
    }
    &__img2 {
      max-width: 9.375rem;
    }
    &__img3 {
      right: 0;
      max-width: 6.25rem;
    }
    &__bottom {
      position: static;
    }
    &__item {
      flex-direction: column;
      text-align: center;
      &-img {
        max-width: 3.125rem;
        height: 3.125rem;
      }
      &-text {
        margin: 1rem 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .page-top {
    h1 {
      font-size: 1.75rem;
      span {
        font-size: 2.5rem;
      }
    }
    p {
      font-size: 1rem;
    }
    &__info {
      display: block;
      margin-top: 2rem;
    }
    &__btn {
      width: 15.625rem;
      height: 3.75rem;
    }
    &__info-text {
      width: 100%;
      max-width: 15.625rem;
      height: auto;
      background: transparent;
      margin-top: 0.5rem;
    }
    &__img2 {
      right: -6rem;
    }
    &__bottom {
      padding: 2rem 0;
    }
    &__items {
      display: grid;
      grid-template-columns: 100%;
    }
    &__item {
      flex-direction: row;
      align-items: start;
      text-align: start;
      &-img {
        height: auto;
      }
      &-text {
        max-width: 100%;
        margin: 0 0 0 1rem;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .page-top {
    &__img2 {
      display: none;
    }
    &__img3 {
      max-width: 5rem;
    }
  }
}
