body {
  font-family: 'RobotoMono', sans-serif;
}
.fixed-body {
  overflow: hidden;
}
.container-fluid {
  max-width: 1700px;
}
.wrapper {
  overflow: hidden;
}
.container-fluid {
  padding-left: 4.0625rem;
}
.wrapper {
  padding-left: 8.75rem;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Tektur';
}


@media screen and (max-width: 1399px) {
  .wrapper {
    padding-left: 5rem;
  }
  .container-fluid {
    padding-left: 2.5rem;
  }
}

@media screen and (max-width: 1399px) {
  .container-fluid {
    padding-left: 3rem;
  }
}

@media screen and (max-width: 1023px) {
  .wrapper {
    padding-left: 0;
  }
  .container-fluid {
    padding-left: 2.5rem;
  }
}

@media screen and (max-width: 767px) {
  .container-fluid {
    padding-left: 2rem;
  }
}
@media screen and (max-width: 499px) {
  .container-fluid {
    padding-left: 1.5rem;
  }
}

@media screen and (max-width: 359px) {
  .container-fluid {
    padding-left: 1rem;
  }
}
