.header {
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  width: calc(100% - 8.75rem);
  padding: 1.6875rem 0 0 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    margin-right: 2rem;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 14rem;
    img {
      width: 100%;
    }
  }
  ul {
    display: none;
  }
  &__btn {
    width: 13.75rem;
    height: 3rem;
    border-radius: 0.625rem;
    margin-right: 4.6875rem;
  }
  &__select {
    display: flex;
    margin-right: 4.25rem;
    &-icon {
      width: 1.125rem;
      margin-top: -0.0625rem;
      img {
        width: 100%;
      }
    }
  }
  .select__wrap {
    position: relative;
  }
  .select__wrap--active {
    .select__list {
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__list-default {
    position: relative;
    z-index: 2;
    max-width: 10rem;
    min-width: 10rem;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
    cursor: pointer;
    .select__current {
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 600;
      text-decoration: underline;
      text-underline-offset: 0.1875rem;
      color: #0072AF;
    }
    .select-arrow {
      flex-shrink: 0;
      width: 0.75rem;
      margin-left: 0.3125rem;
      img {
        width: 100%;
      }
    }
  }
  .select__list {
    position: absolute;
    top: 100%;
    left: 0;
    display: grid;
    width: 100%;
    grid-template-columns: 100%;
    gap: 0.3125rem;
    background: #FFF;
    border: 0.0625rem solid #1397DE;
    border-radius: 0 0 0.3125rem 0.3125rem;
    padding: 0.8125rem 0.5rem 0.5rem;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    option {
      font-size: 0.875rem;
      line-height: 130%;
      color: #4E5960;
      cursor: pointer;
    }
    .li--active {
      font-weight: 600;
      color: #0072AF;
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-right: 4.6875rem;
  }
  &__social {
    svg path {
      transition: all 0.3s;
    }
    &:hover {
      svg path {
        fill: #87CEF4;
      }
    }
  }
  &__group {
    text-align: right;
  }
  &__phone {
    display: block;
    font-family: 'Tektur';
    font-size: 1.5rem;
    line-height: 150%;
    font-weight: 600;
    color: #333 !important;
    &:hover {
      color: #87CEF4 !important;
    }
  }
  &__btn2 {
    background: transparent;
    color: #0095DB;
    padding: 0 !important;
    span {
      font-size: 1rem;
      line-height: 150%;
      font-weight: 700;
      padding: 0;
    }
    &:hover {
      color: #87CEF4;
    }
  }
  &__mob {
    display: none;
  }
  .burger {
    span {
      background: #1397DE;
    }
    &--active {
      span {
        background: #ffffff;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .header {
    &__btn {
      margin-right: 2rem;
    }
    &__select {
      margin-right: 2rem;
    }
    &__socials {
      margin-right: 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .header {
    width: 100%;
    padding-left: 5rem;
    &__left {
      margin-right: 1.5rem;
    }
    &__logo {
      width: 12.5rem;
    }
    &__btn {
      margin-right: 1.5rem;
    }
    &__select {
      margin-right: 1.5rem;
    }
    &__socials {
      margin-right: 1.5rem;
    }
    &__social {
      svg {
        width: 2rem;
        height: auto;
      }
    }
    &__phone {
      font-size: 1.25rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  .header {
    &__btn {
      width: 11.25rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .header {
    padding: 1.25rem 0 0 0;
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background: #1397DE;
      padding: 6.25rem 2rem 3.125rem;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
    }
    ul {
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 1.5rem;
      li {
        font-size: 1rem;
        line-height: 130%;
        color: #ffffff;
      }
      li:not(:last-child) {
        margin-bottom: 1rem;
      }
    }
    .header__btn {
      background: #ffffff;
      margin: 0 0 1.5rem 0;
      span {
        color: #4E5960;
      }
    }
    &__socials {
      margin: 0 0 1.5rem 0;
    }
    &__social {
      svg path {
        fill: #ffffff;
      }
    }
    &__group {
      text-align: center;
    }
    &__phone {
      color: #ffffff !important;
    }
    &__btn2 {
      color: #ffffff;
    }
    &__select {
      display: none;
    }
    &__mob {
      display: flex;
      align-items: center;
      .header__select {
        display: flex;
      }
    }

  }
}

@media screen and (max-width: 576px) {
  .header {
    &__mob {
      .header__select {
        display: none;
      }
    }
  }
}